<template>
  <div>
    <div class="mainTitle">标签列表</div>
    <a-card>
      <a-row :gutter="20">
        <a-col :span="4">
          <a-button type="primary" @click="addTagVisible = true">新增</a-button>
        </a-col>
      </a-row>
      <a-table rowKey="name" :columns="columns" :pagination="pagination" :dataSource="tagList" @change="handleTableChange" bordered style="margin-top:40px;">
        <template slot="action" slot-scope="data">
          <div class="actionSlot">
            <a-button type="primary" icon="edit" @click="editTag(data.id)" style="margin:0 10px;">编辑</a-button>
            <a-button type="danger" icon="delete" @click="deleteTag(data.id)" style="margin:0 10px;">删除</a-button>
          </div>
        </template>
      </a-table>
    </a-card>

    <!--新增标签-->
    <a-modal
        title="新增标签"
        :visible="addTagVisible"
        @ok="addTagOk"
        @cancel="addTagCancel"
        destroyOnClose >
      <a-form-model :model="newTag" :rules="newTagRule" ref="addTagRef">
        <a-form-model-item has-feedback label="标签名" prop="name">
          <a-input v-model="newTag.name"></a-input>
        </a-form-model-item>
        <a-form-model-item label="分类" prop="cid">
          <a-select v-model="newTag.cid" allowClear placeholder="请选择分类" style="width: 200px">
            <a-select-option v-for="item in categoryList" :key="item.id" :value="item.id">{{item.name}}</a-select-option>
          </a-select>
        </a-form-model-item>
      </a-form-model>
    </a-modal>

    <!--编辑标签-->
    <a-modal
        title="编辑标签名称"
        :visible="editTagVisible"
        @ok="editTagOk"
        @cancel="editTagCancel"
        destroyOnClose >
      <a-form-model :model="tagInfo" :rules="tagRule" ref="editTagRef">
        <a-form-model-item has-feedback label="标签名" prop="name">
          <a-input v-model="tagInfo.name"></a-input>
        </a-form-model-item>
        <a-form-model-item has-feedback label="分类" prop="category">
          <a-input disabled v-model="tagInfo.Category.name"></a-input>
        </a-form-model-item>
      </a-form-model>
    </a-modal>
  </div>
</template>

<script>
const columns = [
  {
    title:'ID',
    dataIndex:'id',
    width:'10%',
    key:'id',
    align:"center",
  },
  {
    title:"标签名",
    dataIndex:"name",
    width:"20%",
    key:"name",
    align:"center",
  },
  {
    title:"分类",
    dataIndex:"Category.name",
    width:"20%",
    key:"category",
    align:"center",
  },
  {
    title:"操作",
    width:"20%",
    key:"action",
    align:"center",
    scopedSlots:{customRender:'action'},
  },
]

export default {
  data(){
    return{
      pagination:{
        pageSizeOptions: ['5','10','20'],
        pageSize:5,
        total:0,
        showSizeChanger:true,
        showTotal:(total)=>`共${total}条`,
      },
      tagInfo:{
        id:0,
        name:'',
        Category:{},
      },
      newTag:{
        name:'',
      },
      categoryList:[],
      tagList:[],
      columns,
      visible:false,
      addTagVisible:false,
      editTagVisible:false,
      tagRule:{
        name:[{validator:(rule,value,callback)=>{
            if(this.tagInfo.name === ""){
              callback(new Error("请输入标签名"))
            }
            callback()
          },trigger:"blur"}],
      },
      newTagRule:{
        name:[{validator:(rule,value,callback)=>{
            if(this.newTag.name === ""){
              callback(new Error("请输入标签名"))
            }
            callback()
          },trigger:"blur"}],
        cid:[{validator:(rule,value,callback)=>{
            if(!this.newTag.cid){
              callback(new Error("请选择标签所属的分类"))
            }
            callback()
          },trigger:"blur"}],
      },
    }
  },
  created(){
    this.getTagList()
    this.getCategoryList()
  },

  methods:{
    async getTagList(){
      const { data : res } = await this.$http.get("tag", )
      if(res.status !== 200) return this.$message.error(res.message)
      this.tagList = res.data
    },

    async getCategoryList(){
      const { data : res } = await this.$http.get("categories", {
        params: {
          pagesize:1000,
          pagenum: 1
        }
      })
      if(res.status !== 200) return this.$message.error(res.message)
      this.categoryList = res.data
    },

    handleTableChange(pagination, filters, sorter) {
      var pager = { ...this.pagination }
      pager.current = pagination.current
      pager.pageSize = pagination.pageSize
      this.queryParam.pagesize = pagination.pageSize
      this.queryParam.pagenum = pagination.current

      if (pagination.pageSize !== this.pagination.pageSize) {
        this.queryParam.pagenum = 1
        pager.current = 1
      }
      this.pagination = pager
      this.getCategoryList()
    },

    async deleteTag(id){
      this.$confirm({
        title:"确定要删除吗？",
        content:"一旦删除将不可恢复，真的会消失很久！",
        onOk:async()=>{
          const res = await this.$http.delete(`tag/${id}`)
          if(res.data.status !== 200) {
            this.$message.error("存在文章在当前标签下")
          }else{
            this.$message.success("操作成功")
          }
          this.getTagList()
        },
        onCancel:()=>{
          this.$message.info("取消成功")
        },
      })
    },

    addTagOk(){
      this.$refs.addTagRef.validate(async (valid)=>{
        if(!valid)return this.$message.error("请填写正确信息")
        const { data:res } = await this.$http.post('tag/add', {
          name: this.newTag.name,
          cid:this.newTag.cid
        })
        if(res.status !== 200) return this.$message.error(res.message)
        this.$message.success("创建标签成功")
        this.addTagCancel()
        this.getTagList()
      })
    },

    addTagCancel(){
      this.$refs.addTagRef.resetFields()
      this.addTagVisible = false
    },

    async editTag(id){
      this.editTagVisible = true
      const {data:res} = await this.$http.get(`tag/${id}`)
      this.tagInfo = res.data
    },

    editTagOk(){
      this.$refs.editTagRef.validate(async (valid)=>{
        if(!valid)return this.$message.error("请填写正确信息")
        const { data:res } = await this.$http.put('tag/'+ this.tagInfo.id, {
          name: this.tagInfo.name,
        })
        if(res.status !== 200) return this.$message.error(res.message)
        this.$message.success("更新标签名成功")
        this.editTagCancel()
        this.getTagList()
      })
    },
    editTagCancel(){
      this.$refs.editTagRef.resetFields()
      this.editTagVisible = false
    },
  }
}
</script>
<style scoped>
.mainTitle{
  font-size: calc(20px + 2vw);
  text-align: center;
  align-content: center;
  background-color: white;
  font-family:"方正粗黑宋简体",sans-serif;
  user-select: none;
}
</style>
